import React, { useState, useEffect } from 'react';
import ReactMarkdown from 'react-markdown';

const Markdown = () => {
    const [input, setInput] = useState('');

    const getInput = async () => {
        const instructionsPath = require('./README.md');
        try {
            const instructionsFile = await fetch(instructionsPath);
            const instructionsText = await instructionsFile.text();
            setInput(instructionsText);
            
        } catch (err) {
            console.error('Problem reading markdown file', err);
        }
    };

    useEffect(() => {
        getInput();
    }, [getInput]);

    return (
    <div>
        <div className="container-fluid">
            <div className="container">
                <div className='row justify-content-center'>
                    <div className='col-xs-12 col-sm-12 col-md-6 col-lg-6' style={{textAlign: "left"}}>
                        <ReactMarkdown 
                        escapeHtml={false}
                        children={input} 
                        ></ReactMarkdown>;
                    </div>  
                </div>
            </div>
        </div>   
    </div>
    )
};

export default Markdown;