import React, { useState } from 'react';
import logoFullscreen from './images/fullscreen-st-logo.svg';
import Header from './components/Header';
import DataThroughput from './onglets/DataThroughput';
import HeartRate from './onglets/HeartRate';
import P2Pserver from './onglets/P2Pserver';
import Markdown from './onglets/Markdown';
import Ota from './onglets/Ota';
import { BrowserRouter, Route, Link, Routes } from "react-router-dom";
import './styles/style.css'
import 'bootstrap/dist/css/bootstrap.min.css';
import bootstrap from 'bootstrap';
import { FullScreen, useFullScreenHandle } from "react-full-screen";


const App = () => {
  const handle = useFullScreenHandle();
  const [allServices, setAllServices] = useState([]);
  const [allCharacteristics, setAllCharacteristics] = useState([]);
  const [device, setDevice] = useState();
  const [isDisconnected, setIsDisconnected] = useState(true);
  let listItems = [];
  listItems = [];
  
allServices.map(service => {

  if(service.service.uuid === "0000fe80-8e22-4541-9d4c-21edae82ed19"){
      listItems.push(<li className='liProfile'><Link to="/DT">Data Throughput</Link></li>);
  }
  if(service.service.uuid === "0000180d-0000-1000-8000-00805f9b34fb"){
      listItems.push(<li className='liProfile'><Link to="/HR">Heart Rate</Link></li>);
  }
  if(service.service.uuid === "0000fe40-cc7a-482a-984a-7f2ed5b3e58f"){
      listItems.push(<li className='liProfile'><Link to="/P2P">P2P Server</Link></li>);
  }
  if(service.service.uuid === "0000fe20-cc7a-482a-984a-7f2ed5b3e58f"){
      listItems.push(<li className='liProfile'><Link to="/OTA">Firmware Update Over The Air</Link></li>);
  }
});


  return (
    // <FullScreen handle={handle}>
      <BrowserRouter>
        <div>
          {/* <img src={logoFullscreen} onClick={handle.enter} id='fullscreenButton'></img> */}
            <Header setIsDisconnected={setIsDisconnected} setAllServices={setAllServices} setAllCharacteristics={setAllCharacteristics} setDevice={setDevice}></Header>
              <ul className='ulProfile'>{listItems}</ul>
            {/* if device isDisconnected do render nothing more, else render component services contain in onglets[] */}
            {/* {isDisconnected ? null : onglets} */}
            <div className="main-route-place">
              <Routes>
                <Route path="/" element={<Markdown></Markdown>}/>
                <Route path="*"/>
                {/* If isDisconnected === true, element = null else element = <component></component> */}
                <Route path="/HR" element={isDisconnected ? null : <HeartRate allCharacteristics={allCharacteristics} device={device}></HeartRate>} />
                <Route path="/P2P" element={isDisconnected ? null : <P2Pserver allCharacteristics={allCharacteristics} device={device}></P2Pserver>} />
                <Route path="/OTA" element={isDisconnected ? null : <Ota allCharacteristics={allCharacteristics} device={device}></Ota>} />
                <Route path="/DT" element={isDisconnected ? null : <DataThroughput allCharacteristics={allCharacteristics} device={device}></DataThroughput>} />
              </Routes>
            </div>
        </div>
      </BrowserRouter>
    // </FullScreen>

  );
}

export default App;
